import React from 'react';

class Setting extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
    };
  }

  handleChangeName(event) {
    this.setState({
      username: event.target.value,
    });
  }

  render() {
    return (
      <div className={this.props.isOpen ? 'mdl show' : 'mdl'}>
        <div className="mdl-mask" onClick={this.props.handleClose}></div>
        <div className="sidebar">
          <h3>Setting</h3>
          <hr />

          <fieldset>
            <legend>ì‚¬ìš©ì ì„¤ì •</legend>
            <span className="small-margin-right">ì‚¬ìš©ì ì´ë¦„</span>
            <input
              type="text"
              className="small-margin-right"
              value={this.state.username}
              onChange={this.handleChangeName}
            />
            <button onClick={this.props.handleClose}>ë‹«ê¸°</button>
          </fieldset>

          <hr />
        </div>
      </div>
    );
  }
}

export default Setting;
